import React from 'react';

class Register extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            email: '',
            password: '',
            name: ''
        }
    }


    onNameChange = (event) => {
        this.setState({name: event.target.value})
    }
    onEmailChange = (event) => {
        this.setState({email: event.target.value})
    }

    onPasswordChange = (event) => {
        this.setState({password: event.target.value})
    }

    onSubmitSignIn = () => {
        console.log(this.state);
        fetch('https://safe-chamber-28409.herokuapp.com/register', {
            method:'post',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({
                email: this.state.email,
                password:this.state.password,
                name:this.state.name
            })
        })
        .then(response => response.json())
        .then(user => {
            if (user.id) { // if get the user back
                this.props.loadUser(user);
                this.props.onRouteChange('home');
            }
        })
    }

    render() {
        // const { onRouteChange } = this.props;
        return (
            <div>
                <main className="ba b--white-10 center br2 mv4 w-199 w-50-m w-25-1 mw6">
                <div className="measure mt4 mb4">
                    <fieldset id="sign_up" className="ba b--transparent ph0 mh0" >
                        <legend className="f4 fw6 ph0 mh0 white">Register</legend>
                        <div className="mt3">
                            <label className="db fw6 lh-copy f6 white" htmlFor="name">Name</label>
                            <input 
                                className="pa2 input-reset ba bg-transparent hover-bg-gray hover-white w-100" 
                                type="text" 
                                name="name"  
                                id="name" 
                                onChange={this.onNameChange}
                            />
                        </div>
                        <div className="mt3">
                            <label className="db fw6 lh-copy f6 white" htmlFor="email-address">Email</label>
                            <input 
                                className="pa2 input-reset ba bg-transparent hover-bg-gray hover-white w-100" 
                                type="email" 
                                name="email-address"  
                                id="email-address" 
                                onChange={this.onEmailChange}
                            />
                        </div>
                        <div className="mv3">
                            <label className="db fw6 lh-copy f6 white" htmlFor="password">Password</label>
                            <input 
                                className="b pa2 input-reset ba bg-transparent hover-bg-gray hover-white w-100" 
                                type="password" 
                                name="password"  
                                id="password" 
                                onChange={this.onPasswordChange}
                            />
                        </div>
                    </fieldset>
                    <div className="">
                        <input 
                            onClick={this.onSubmitSignIn} 
                            className="b ph3 pv2 input-reset ba b--white bg-transparent grow pointer f6 dib white pointer" 
                            type="submit" 
                            value="Register" 
                        />
                    </div>
    
                </div>
                </main>
            </div>
        );
    }
    
}

export default Register;